.footer{
  padding: 6.875em 0;
  color: #fff;
  background-color: $black;
  &__top{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
  &__logo{
    display: block;
    width: 12.5em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.footer-nav{
  width: 28em;
  max-width: 100%;
  display: grid;
  grid-template-columns: repeat(2,calc(50% - 2em));
  gap: 1.25em 4em;
  &__link{
    color: #fff;
    padding: .41em 0;
    font-weight: 500;
    span{
      font-size: 1.125em;
      letter-spacing: -0.0225em;
    }
    &._border{
      border-bottom: 1px solid #fff;
      display: inline-flex;
      margin-right: auto;
      &:hover{
        border-color: $primary;
      }
    }
  }
}

.footer-contacts{
  width: calc(100% / 3 - 1.25em);
  @include grid100gap(1.25em);
  &__item{
    display: flex;
    align-items: center;
  }
  &__icon{
    margin-right: 1.25em;
    width: 2.75em;
    height: 2.75em;
    flex-shrink: 0;
    border: 2px solid #fff;
    @include center;
    img{
      width: 1.5em;
    }
  }
  &__content{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__btn{
    font-weight: 500;
    letter-spacing: -0.0175em;
    color: #fff;
    border-bottom: 1px dashed #fff;
    &:hover{
      color: $primary;
      border-color: $primary;
    }
  }
  .socials{
    &__item{
      path{
        fill: #fff;
      }
      &:hover{
        path{
          fill: $primary;
        }
      }
    }
  }
}

.members{
  margin-top: 3.75em;
}

.member-card{
  background-color: #231F20;
  padding: 1.875em;
  &__content{
    @include grid100gap(1.25em);
  }
  &__desc{
    color: #F5F5F5;
  }
  &__img{
    margin-top: 1.25em;
    height: 3.75em;
    display: block;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 3.75em 0;
    &__top{
      display: block;
    }
  }

  .footer-nav{
    margin-top: 1.875em;
  }

  .footer-contacts{
    width: 100%;
    margin-top: 2.5em;
  }

  .members{
    margin-top: 2.5em;
    grid-template-columns: repeat(2,calc(50% - .25em));
    gap: .5em;
  }

  .member-card{
    &__title{
      font-size: 1.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__logo{
      width: 9.5em;
    }
  }

  .footer-nav{
    grid-template-columns: 1fr .7fr;
    gap: .625em 3em;
    &__link{
      span{
        font-size: 1em;
      }
    }
  }

  .members{
    grid-template-columns: 100%;
  }
}
