.header{
  padding: 1.875em 0;
  position: relative;
  z-index: 10;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    width: 12.5em;
    display: block;
    svg{
      width: 100%;
      height: auto;
      display: block;
    }
  }
  &__desc{
    display: none;
  }
}

.header-contacts{
  display: flex;
  align-items: center;
  &__phone,
  &__address{
    display: flex;
    align-items: center;
    &-icon{
      width: 2.75em;
      height: 2.75em;
      border: 2px solid $primary;
      @include center;
      margin-right: 1.25em;
      flex-shrink: 0;
      img{
        width: 1.5em;
        display: block;
      }
    }
  }
  &__address,
  &__phone{
    &-btn,
    &-city{
      cursor: pointer;
      font-weight: 500;
      letter-spacing: -0.0175em;
      color: $primary;
      border-bottom: 1px dashed $primary;
    }
  }
  &__address{
    &-title{
      color: #939598;
      margin-right: .31em;
    }
  }
  &__phone{
    margin-left: 3.125em;
    &-content{
      display: flex;
      flex-direction: column;
      align-items: flex-start;
    }
    &-btn{
      margin-top: .2em;
    }
  }
  .socials{
    margin-left: 3.125em;
  }
}

.socials{
  display: flex;
  align-items: center;
  &__items{
    display: flex;
    margin-left: .94em;
  }
  &__item{
    display: block;
    width: 2.25em;
    @include lastM(0 .625em 0 0);
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      path{
        fill: $black;
      }
    }
  }
}

.menu-contacts,
.burger{
  display: none;
}

.menu{
  display: flex;
  background-color: #231F20;
  margin-top: 1.25em;
  color: #fff;
  &__btn{
    height: 3.25em;
    flex-grow: 1;
    padding: 0 1.5em;
    span{
      transform: translateY(5%);
    }
  }
}

.nav{
  display: flex;
  align-items: center;
  padding: 0 1.625em;
  &__link{
    display: flex;
    align-items: center;
    &:last-child{
      &:after{
        display: none;
      }
    }
    span{
      font-size: 1.125em;
      line-height: 120%;
      letter-spacing: -0.0225em;
      transform: translateY(5%);
    }
    &:after{
      content: '';
      width: 0.0625em;
      height: 1.5em;
      background-color: #939598;
      margin: 0 1.65em;
      flex-shrink: 0;
      pointer-events: none;
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    padding: 0;
    .container{
      padding: 0;
    }
    &__top{
      display: grid;
      grid-template-columns: 100%;
    }
    &__mob{
      padding: .5em 1em;
      background-color: $primary;
      color: #fff;
      display: flex;
      align-items: center;
    }
    &__logo{
      width: 6.5em;
      margin-right: 1.25em;
      path{
        fill: #fff;
      }
    }
    &__desc{
      max-width: 8em;
      display: block;
      .text14{
        line-height: 100%;
      }
    }
    @media screen and (min-width: 577px){
      &__logo,
      &__desc,
      .burger{
        font-size: 1.5em;
      }
    }
    &._open{
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }

  .header-contacts{
    order: -1;
    padding: 1em;
    justify-content: space-between;
    @media screen and (min-width: 577px){
      .header-contacts__address,
      .header-contacts__phone,
      .socials{
        font-size: 1.75em;
      }
    }
    .header-contacts__address{
      &-icon{
        width: unset;
        height: unset;
        margin-right: .25em;
        border: none;
        img{
          width: 1em;
        }
      }
      &-title{
        display: none;
      }
      &-city{
        font-size: .625em;
      }
    }
    .header-contacts__phone{
      order: 2;
      margin: 0;
      &-icon{
        display: none;
      }
      &-tel{
        font-size: .75em;
        line-height: 100%;
      }
      &-btn{
        margin: 0;
        font-size: .5em;
      }
    }
    .socials{
      margin: 0;
      .text16{
        display: none;
      }
      &__items{
        margin: 0;
      }
      &__item{
        width: 1em;
        @include lastM(0 .25em 0 0);
      }
    }
  }

  .burger{
    margin-left: auto;
    display: flex;
    width: 2.25em;
    height: 2.25em;
    position: relative;
    img{
      width: 100%;
      @include centerAbsolute;
    }
    ._view2{
      opacity: 0;
    }
  }

  .menu{
    height: calc(100 * var(--vh) - 8.75em);
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 0 1.25em 3.75em;
    overflow-y: auto;
    flex-direction: column;
    align-items: stretch;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    &__btn{
      width: 100%;
      flex-shrink: 0;
    }
  }

  .nav{
    display: block;
    padding: 0;
    &__link{
      padding: 1.25em 0;
      &:after{
        display: none;
      }
      &:not(:last-child){
        border-bottom: 1px solid #939598;
      }
    }
  }

  .menu-contacts{
    display: block;
    padding-top: 3em;
    margin-top: auto;
    .header-contacts__phone{
      margin: 0;
      &-icon{
        border-color: #fff;
      }
      &-btn{
        color: #fff;
        border-color: #fff;
      }
    }
    .socials{
      margin-top: 1.69em;
      path{
        fill: #fff;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .menu{
    height: calc(100 * var(--vh) - 6.625em);
  }
}
