.gray-bg{
  background-color: #F5F5F5;
}

.main{
  padding: 5.37em 0;
  &__inner{
    display: grid;
    grid-template-columns: 39.0625em calc(100% - 39.0625em - 4.375em);
    gap: 4.375em;
  }
  &__content{
    padding-top: 1.31em;
    position: relative;
    z-index: 3;
  }
  &__top{
    position: relative;
  }
  &__title{
    button{
      text-align: left;
      display: block;
      color: $primary;
      text-decoration: underline;
      text-decoration-style: dashed;
    }
  }
  &__badge{
    position: absolute;
    display: flex;
    align-items: center;
    height: 3.0625em;
    background-color: $black;
    font-family: $font2;
    color: #fff;
    white-space: nowrap;
    padding: 0 1.25em;
    font-weight: 700;
    @media screen and (min-width: 993px){
      &._wallet{
        bottom: 1.25em;
        left: 100%;
        margin-left: .25em;
      }
      &._percent{
        left: 100%;
        margin-left: -3.17em;
        top: .5em;
      }
    }
    span{
      font-size: 1.875em;
      transform: translateY(5%);
    }
    img{
      margin-right: .625em;
      width: 1.8125em;
      display: block;
      flex-shrink: 0;
    }
  }
  &__items{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .9375em));
    gap: 1.875em;
  }
  &__item{
    display: flex;
    align-items: center;
    &:before{
      content: '';
      flex-shrink: 0;
      background-image: url(../img/check.svg);
      @include bgImgSize(100%);
      width: 2.125em;
      height: 2.125em;
      margin-right: .625em;
    }
    b{
      font-weight: 900;
    }
  }
  &__img{
    position: relative;
    &-triangle{
      position: absolute;
      top: -3.25em;
      right: -2.31em;
      width: 15.9375em;
    }
    &-car{
      width: 100%;
      display: block;
      position: relative;
      z-index: 2;
    }
  }
  .success-form{
    margin-top: 2.5em;
  }
}

.main-form{
  margin-top: 2.5em;
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .9375em));
  gap: 1.25em 1.875em;
  ._full{
    grid-column: 1/-1;
  }
}

.calc{
  padding: 6.87em 0 5em;
  &__grid{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .9375em));
    gap: 1.875em;
    align-items: flex-start;
  }
  .success-form{
    margin-top: 1.25em;
  }
}

.calc-group{
  @include grid100gap(1.25em);
  &__title{
    font-weight: 700;
    line-height: 140%;
  }
  &__output{
    width: 13.8125em;
    height: 3.8125em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 1.25em;
    background-color: #F5F5F5;
    border-radius: .3125em;
  }
}

.filter-slider{
  &__nums{
    display: flex;
    justify-content: space-between;
    margin-top: .625em;
  }
  &__num{
    font-size: 1em;
    line-height: 140%;
  }
  &__item{
    min-height: 1.5em;
    padding: 0 .125em;
    display: flex;
    align-items: center;
  }
  .range-slider{
    width: 100%;
    background-color: #F5F5F5;
    border: none;
    box-shadow: none;
    height: .25em;
    border-radius: 0;
    .noUi-connect{
      background-color: $primary;
      border-radius: 0;
    }
    .noUi-handle{
      box-shadow: none;
      background-image: url(../img/handle.svg);
      @include bgImgSize(100%);
      background-color: transparent;
      border: none;
      width: 1.5em;
      height: 1.5em;
      top: -.625em !important;
      right: -.625em !important;
      border-radius: 50%;
      cursor: pointer;
      &:before{
        opacity: 0;
        width: 300%;
        height: 100%;
        @include centerAbsolute;
      }
      &:after{
        display: none;
      }
    }
  }
}

.offer{
  &__items{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .4em));
    gap: 1em .8em;
  }
  &__hint{
    margin-top: -.625em;
    color: #939598;
  }
}

.offer-item{
  padding-left: 1.25em;
  border-left: 1px solid $primary;
  &__title{
    font-size: 1em;
    line-height: 140%;
    color: #939598;
  }
  &__price{
    display: flex;
    align-items: flex-end;
  }
  &__num{
    font-size: 1.5em;
    line-height: 140%;
    font-weight: 700;
    color: $primary;
  }
  &__currency{
    font-size: .875em;
    line-height: 140%;
    margin-left: .5em;
    margin-bottom: .4em;
  }
}

.calc-req{
  &__form{
    margin-top: 1.25em;
    display: grid;
    grid-template-columns: calc(100% - 13.25em - 1.875em) 13.25em;
    gap: 1.875em;
  }
}

.success-form{
  background-color: #158F44;
  padding: 1.375em 1.25em;
  color: #fff;
  &__text{
    font-weight: 700;
    p{
      font-size: 1.125em;
      @include lastM(0 0 1em);
    }
  }
  &__socials{
    display: flex;
    align-items: center;
    margin-top: 1.25em;
    path{
      fill: #fff;
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    padding: 1.875em 0 2.44em;
    &__inner{
      grid-template-columns: 100%;
      gap: .94em;
    }
    &__content{
      padding: 0;
    }
    &__badges{
      display: flex;
      align-items: center;
      margin-top: 1.25em;
    }
    &__badge{
      position: static;
      height: 2em;
      padding: 0 1.12em;
      @include lastM(0 .375em 0 0);
      img{
        width: .75em;
      }
      span{
        font-size: .875em;
      }
    }
    .success-form,
    &__top{
      @media screen and (min-width: 577px){
        font-size: 1.5em;
      }
    }
    &__img{
      width: calc(100% - 3.5em);
      max-width: 30em;
      margin: 0 auto;
      &-triangle{
        width: 8.3125em;
        top: -.94em;
        right: -2.19em;
      }
    }
  }

  .calc{
    padding: 3.75em 0;
    &__grid{
      margin-top: 1.875em;
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
    }
    .success-form{
      margin-top: 1em;
    }
  }

  .calc-group{
    gap: 1em;
    &__title{
      font-size: 1.125em;
    }
    &__output{
      height: 3.5em;
      width: 100%;
      .text18{
        font-size: .875em;
      }
    }
  }

  .filter-slider{
    ._hide-tablet{
      display: none;
    }
  }

  .offer{
    &__items{
      grid-template-columns: 100%;
    }
    &__hint{
      margin-top: 0;
    }
  }

  .calc-req{
    &__form{
      margin-top: 1em;
      grid-template-columns: 100%;
      gap: 1em;
    }
  }

  .success-form{
    padding: 1.25em;
    &__text{
      p{
        font-size: 1em;
      }
    }
    &__socials{
      align-items: flex-start;
      .text16{
        font-size: .75em;
        line-height: 110%;
      }
      .socials__item{
        width: 1.5em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    &__title{
      width: calc(100% + .5em);
    }
    &__items{
      margin-top: 1.875em;
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
    }
    &__item{
      &:before{
        margin-right: .25em;
        width: 1.25em;
        height: 1.25em;
      }
      .text24{
        font-size: .75em;
      }
    }
    .success-form{
      margin-top: 1.875em;
    }
  }

  .main-form{
    margin-top: 1.875em;
    grid-template-columns: 100%;
    gap: .625em;
  }

  .calc{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .calc-group{
    padding-bottom: 1.5em;
    border-bottom: 1px solid #D9DBDE;
  }
}
