.stages,
.examples,
.conditions{
  padding: 6.875em 0;
  &__grid{
    margin-top: 2.5em;
  }
}

.stage-card{
  background-color: #FFF;
  padding: 2.5em 1.875em;
  &__icon{
    @include center;
    width: 3.75em;
    height: 3.75em;
    border: 2px solid $primary;
    img{
      width: 2.5em;
    }
  }
  &__content{
    margin-top: .94em;
    @include grid100gap(.94em);
  }
}

.example-card{
  display: grid;
  grid-template-columns: 17.375em calc(100% - 17.375em - 1.875em);
  gap: .9375em 1.875em;
  padding: 1.25em;
  background-color: #F5F5F5;
  &__img{
    @include imgCover(64%)
  }
  &__content{
    display: flex;
    flex-direction: column;
    padding-top: .625em;
  }
  &__props{
    margin-top: auto;
    padding-top: 1.25em;
    @include grid100gap(.625em);
  }
  &__prop{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.article{
  padding: 6.875em 0;
}

.text{
  h3,h4{
    font-size: 1em;
    line-height: 140%;
    margin-bottom: .625em;
  }
  h3{
    font-weight: 700;
  }
  p,li{
    font-size: .875em;
    line-height: 140%;
    font-weight: 300;
  }
  ul,
  p{
    @include lastM(0 0 1.25em);
  }
  hr{
    display: block;
    height: 1.25em;
    opacity: 0;
  }
  ul{
    @include grid100gap(.5em);
    li{
      position: relative;
      padding-left: 1.43em;
      &:before{
        content: '';
        position: absolute;
        top: .357em;
        left: 0;
        width: .714em;
        height: .714em;
        background-color: $primary;
        border-radius: 50%;
      }
    }
  }
  i{
    color: #939598;
    font-weight: 500;
    font-style: italic;
  }
}

.condition-card{
  padding: 2.5em 1.875em;
  background-color: #fff;
  &__content{
    @include grid100gap(.625em);
  }
  &__list{
    margin-top: 1.25em;
    @include grid100gap(.625em);
    li{
      min-height: 1.875em;
      padding: 0 0 0 3.125em;
      position: relative;
      display: flex;
      align-items: center;
      &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 1.875em;
        height: 1.875em;
        background-color: $primary;
        background-image: url(../img/check2.svg);
        @include bgImgSize(1.25em);
      }
      span{
        font-size: .875em;
        line-height: 140%;
      }
    }
  }
}

.credit{
  color: #fff;
  &__inner{
    background-image: url(../img/credit-bg.webp);
    background-size: cover;
    background-position: center;
    position: relative;
    background-color: #080302;
  }
  &__img{
    position: absolute;
    max-width: 33.375em;
    max-height: 100%;
    display: block;
    right: 7.75em;
    bottom: 0;
  }
  &__content{
    padding: 5em 3.75em 3em;
    max-width: 45em;
  }
  &__desc{
    margin-top: 1.875em;
  }
  .condition-card__list{
    margin-top: 1.875em;
    grid-template-columns: repeat(3,calc(100% / 3 - 5em / 3));
    gap: 1.25em 2.5em;
  }
  &__nav{
    display: flex;
    margin-top: 3.75em;
  }
}


@media screen and (max-width: 992px){
  .stages{
    padding: 3.75em 0;
    &__grid{
      margin-top: 1.875em;
      grid-template-columns: repeat(3,calc(100% / 3 - 1em / 3));
      gap: .5em;
    }
  }

  .stage-card{
    padding: 1.875em 1.25em;
  }

  .conditions,
  .examples{
    padding: 3.75em 0;
    &__grid{
      margin-top: 1.875em;
      grid-template-columns: repeat(2,calc(50% - .25em));
      gap: .5em;
    }
  }

  .example-card{
    grid-template-columns: 100%;
    &__content{
      padding: 0;
    }
  }

  .article{
    padding: 3.75em 0;
  }

  .text {
    h3, h4 {
      font-size: .875em;
    }
    h3{
      margin-bottom: 1.43em;
    }
    p, li {
      font-size: .75em;
    }
    hr{
      height: .625em;
    }
  }

  .condition-card{
    padding: 1.875em 1.25em;
  }

  .credit{
    &__content{
      padding: 3.75em 1.25em 0;
    }
    &__img{
      position: static;
      margin: -1em auto 0;
      width: 100%;
      max-width: 25em;
    }
    .condition-card__list{
      margin-top: 1.25em;
    }
    &__nav{
      margin-top: 1.875em;
    }
  }
}


@media screen and (max-width: 576px) {
  .stages {
    &__grid {
      grid-template-columns: 100%;
    }
  }

  .conditions,
  .examples{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .example-card{
    br{
      display: none;
    }
  }

  .credit{
    &__inner{
      background-image: url(../img/credit-bg2.webp);
    }
    .condition-card__list{
      grid-template-columns: 100%;
      gap: .625em;
    }
  }
}
