.fg {
  position: relative;
  margin: 0;
  input {
    border: 1px solid #D9DBDE;
    padding: 0 1.875em;
    height: 3.375em;
    width: 100%;
    font-size: 1em;
    background-color: #fff;
    color: #000;
    &::placeholder {
      color: #6B6D70;
    }
    &:focus {
      border-color: $black;
    }
  }
}

.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check{
  label{
    position: relative;
    cursor: pointer;
    display: block;
    span{
      font-size: .75em;
      line-height: 120%;
    }
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 0;
      transition: all .100s;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
    }
  }
  &._error{
    input:not(:checked){
      &~label:before{
        border-color: #FF1515;
      }
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-color: $black;
        border-color: $black;
        background-size: 1em auto;
      }
    }
  }
  label {
    padding-left: 1.875em;
    min-height: 1.25em;
    &:before {
      border: 2px solid #D9DBDE;
      width: 1.25em;
      height: 1.25em;
      @include bgImgSize(0);
      background-image: url(../img/check2.svg);
      transition: 0s;
    }
  }
}
