.contacts{
  padding: 6.875em 0;
  &__inner{
    display: grid;
    grid-template-columns: 45.875em calc(100% - 47.75em);
    gap: 1.875em;
    margin-top: 3.25em;
  }
  &__places{
    max-height: 27.5em;
    width: calc(100% + .5em);
    padding-right: .5em;
    margin-top: 1.25em;
    overflow-y: auto;
    @include scroll;
    @include grid100gap(.3125em);
  }
}

.map{
  height: 37.5em;
  width: 100%;
}

.city-select{
  margin-top: 1.25em;
  position: relative;
  z-index: 2;
  &._open{
    .city-select__dropdown{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__show{
    cursor: pointer;
    position: relative;
    z-index: 3;
    padding: 0 1.875em;
    display: flex;
    align-items: center;
    background-color: #fff;
    height: 5.25em;
  }
  &__icon{
    width: 2.75em;
    height: 2.75em;
    border: 2px solid $primary;
    @include center;
    margin-right: 1.25em;
    flex-shrink: 0;
    img{
      width: 1.5em;
      display: block;
    }
  }
  &__info{
    display: flex;
    align-items: center;
  }
  &__name{
    font-weight: 500;
    letter-spacing: -0.0175em;
    color: $primary;
    border-bottom: 1px dashed $primary;
  }
  &__title{
    color: #939598;
    margin-right: .31em;
  }
  &__arrow{
    margin-left: auto;
    width: 1.5em;
  }
  &__dropdown{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    padding: 5.25em 1.875em 1.875em;
    box-shadow: 0 .6875em 1.5625em 0 rgba(0, 0, 0, 0.05);
    background-color: #fff;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
  }
  &__variants{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 1.25em 4em;
    background-color: #F5F5F5;
    max-height: 18.5em;
    overflow-y: auto;
    @include scroll;
  }
  .city-item{
    @include lastM(0 0 1.25em);
  }
}

.city-item{
  font-weight: 500;
  span{
    font-size: 1.125em;
    line-height: 144%;
    letter-spacing: -0.0225em;
  }
  &._active{
    color: $primary;
  }
}

.address-card{
  padding: 1.875em 1.625em;
  background-color: #fff;
  @include grid100gap(1.25em);
  &__place,
  &__contacts,
  &__socials,
  &__phone{
    display: flex;
    align-items: center;
  }
  &__socials,
  &__phone{
    &:after{
      content: '';
      width: 0.0625em;
      height: 1.25em;
      flex-shrink: 0;
      margin: 0 .75em;
      background-color: #D9DBDE;
    }
    &:last-child{
      &:after{
        display: none;
      }
    }
  }
  &__place,
  &__phone{
    img{
      width: 1.5em;
      flex-shrink: 0;
      margin-right: .75em;
    }
  }
  &__social{
    width: 1.5em;
    flex-shrink: 0;
    @include lastM(0 .5em 0 0);
    img{
      width: 100%;
      display: block;
    }
  }
}

.faq{
  padding: 6.875em 0;
  &__items{
    margin-top: 2.5em;
  }
}

.qa-card{
  padding: .625em 0;
  border-bottom: 1px solid #D9DBDE;
  &._active{
    .qa-card__show{
      color: $primary;
    }
    .qa-card__btn{
      ._view1{
        opacity: 0;
      }
      ._view2{
        opacity: 1;
      }
    }
  }
  &__show{
    cursor: pointer;
    padding: .625em 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__btn{
    margin-left: .5em;
    flex-shrink: 0;
    width: 2.875em;
    height: 2.875em;
    @include center;
    border: 2px solid #D9DBDE;
    position: relative;
    svg{
      @include centerAbsolute;
      width: 1.625em;
      height: auto;
    }
    ._view2{
      opacity: 0;
    }
    &:hover{
      border-color: $black;
      path{
        fill: $primary;
      }
    }
  }
  &__hidden{
    padding-top: .625em;
    display: none;
  }
  &__text{
    font-size: 1em;
    line-height: 140%;
    font-weight: 300;
  }
}

.reviews{
  padding: 6.875em 0;
}

.reviews-slider{
  margin-top: 2.5em;
  position: relative;
  &__wrap{
    overflow: hidden;
  }
  .swiper{
    &-container{
      width: calc(100% + 1.875em);
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      width: calc(100% / 3);
      height: auto;
      padding-right: 1.875em;
    }
    &-nav{
      display: flex;
      align-items: center;
      position: absolute;
      bottom: 100%;
      right: 0;
      margin-bottom: 3.26em;
    }
    &-button{
      margin: 0;
      position: static;
      &-next{
        margin-left: 1.25em;
      }
    }
  }
}

.swiper-button{
  flex-shrink: 0;
  width: 2.875em;
  height: 2.875em;
  @include center;
  border: 2px solid #D9DBDE;
  position: relative;
  svg{
    width: 1.625em;
    height: auto;
  }
  &:hover{
    border-color: $black;
    path{
      fill: $primary;
    }
  }
}

.review-card{
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 2.5em 1.875em;
  background-color: #fff;
  &__top{
    display: flex;
    align-items: center;
  }
  &__num{
    flex-shrink: 0;
    @include center;
    width: 3.75em;
    height: 3.75em;
    border-radius: 50%;
    background-color: $primary;
    color: #fff;
    font-weight: 700;
    margin-right: .625em;
    span{
      font-size: 1.25em;
      transform: translateY(5%);
    }
  }
  &__text{
    margin: 1.25em 0;
    .text16{
      @include truncate(9);
    }
  }
  &__site{
    border-top: 1px solid #D9DBDE;
    margin-top: auto;
    padding-top: 1.25em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &-img{
      height: 1.875em;
      display: block;
      img{
        max-height: 100%;
        max-width: 100%;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .contacts{
    padding: 3.75em 0;
    &__inner{
      margin-top: 1.875em;
      grid-template-columns: 100%;
    }
    &__map{
      width: calc(100% + 2.5em);
      margin-left: -1.25em;
    }
    &__places{
      width: calc(100% + .375em);
      padding-right: 0;
    }
  }

  .map{
    height: 22em;
  }

  .faq{
    padding: 3.75em 0;
    &__items{
      margin-top: 1.875em;
    }
  }

  .qa-card{
    &__text{
      font-size: .875em;
    }
  }

  .reviews{
    padding: 3.75em 0;
  }

  .reviews-slider{
    margin-top: 1.875em;
    .swiper{
      &-container{
        width: calc(100% + 1em);
      }
      &-slide{
        width: 50%;
        padding-right: 1em;
      }
      &-nav{
        position: static;
        justify-content: flex-end;
        margin: 1.875em 0 0;
      }
    }
  }

  .review-card{
    padding: 1.875em 1.25em;
  }
}


@media screen and (max-width: 576px){
  .city-select{
    &__info{
      flex-direction: column;
      align-items: flex-start;
    }
    &__variants{
      padding-left: 1em;
      padding-right: 1em;
    }
  }

  .address-card{
    padding: 1.875em 1.25em;
    &__contacts{
      margin-top: -.5em;
      flex-wrap: wrap;
    }
    &__socials,
    &__phone{
      margin-top: .75em;
    }
    &__socials{
      &:after{
        display: none;
      }
    }
  }

  .reviews-slider{
    .swiper{
      &-slide{
        width: 100%;
      }
    }
  }
}
