@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1.5em;
    height: 3.375em;
    font-weight: 500;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.125em;
        line-height: 120%;
        letter-spacing: -0.0225em;
    }
}

.m-btn-red{
    background-color: $primary;
    color: #fff;
    &:hover{
        background-color: darken($primary,10%);
        color: #fff;
    }
}
